.my-container {
  background-color: #e9ebee !important;
  display: inline-block;
}

.header-view {
  background-color: #343a40;
}

.university-information {
  display: flex;
  max-width: 1200px;
  padding: 0 0 2rem;
}

.university-image {
  min-width: 200px;
}

.my-logo {
  display: block;
  max-width: 200px;
  max-height: 200px;
  width: auto;
  height: auto;
  border: 1px solid #fcfcfc;
}

.university-information-details-button-container {
  justify-content: flex-end;
  align-items: center;
  display: flex;
}

.university-information-details {
  display: block;
  width: 100%;
  padding: 0 0 0 1rem;
}

.university-information-details-name {
  font-size: 2rem;
  font-weight: bold;
  color: #fcfcfc;
}

.university-information-details-departments {
  font-size: 1.25rem;
  color: #fcfcfc;
}

.university-information-details-location {
  font-size: 1.25rem;
  color: #fcfcfc;
}

.university-information-details-praise-and-blame {
  font-size: 1.25rem;
  font-weight: 500;
}

.university-information-details-praise {
  text-decoration: underline;
}

.university-information-details-blame {
  margin-left: 1rem;
  text-decoration: underline;
}

.university-information-details-button-review {
  background-color: transparent;
  color: #fcfcfc;
  border: 1px solid #fcfcfc;
  border-radius: 5px;
  min-width: 8rem;
  max-width: 8rem;
  max-height: 2.5rem;
  min-height: 2.5rem;
  font-size: 1rem;
  font-weight: 400;
}

.university-information-details-button-review:hover {
  font-weight: 600;
  border-width: 0;
  color: #138496;
  box-shadow: 0 0 0 1pt #138496;
}

.line {
  width: 100%;
  border-bottom: 3px solid black;
  position: absolute;
}

.review-number {
  font-size: 1.75rem;
  font-weight: bold;
  padding: 0.5rem 3rem 0.5rem;
}

.image-animation {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 120px;
  height: 120px;
  margin: -60px 0 0 -60px;
  -webkit-animation: spin 4s linear infinite;
  -moz-animation: spin 4s linear infinite;
  animation: spin 4s linear infinite;
}

/* ===========================================================================
/*                              RESPONSIVE
/* ===========================================================================

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  .university-information-details-button-review {
    margin-top: 0.5rem;
  }

  .university-information {
    display: flex;
    flex-wrap: wrap;
  }

  .university-information-details-button-container {
    height: 60px;
    justify-content: center;
    align-items: center;
  }
}

